@import "fonts";



html,
body {
  overflow-x: hidden;
}

//svg | display: none;
.svg-accountingicon,
.svg-water,
.svg-pers,
.svg-temperature,
.svg__arrow-right{
  display: none;
}

/* Указываем box sizing */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Убираем внутренние отступы */
ul[class],
ol[class] {
  padding: 0;
}

/* Убираем внешние отступы */
body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}
